import { NotificationSchedule } from "@rp/common/api-types"
import { useBriefConfigForm } from "@rp/react/common/brief"
import {
  NotificationsSelectTime,
  NotificationsSelectTimezone,
} from "@rp/react/web/common/composite-components"
import { Box } from "@rp/react/web/mui/core"
import { useBriefNotificationTimeStepData } from "./BriefNotificationsTimeStep.hooks"

type BriefNotificationsTimeStepProps = {
  schedule: NotificationSchedule
  setSchedule: (schedule: NotificationSchedule) => void
}

const BriefNotificationsTimeStep = ({ schedule, setSchedule }: BriefNotificationsTimeStepProps) => {
  const { width, containerSx, timezoneContainerSx, timezoneMenuStyle } =
    useBriefNotificationTimeStepData()
  const { setScheduleFromNotification } = useBriefConfigForm()

  return (
    <Box sx={containerSx}>
      <Box width={width}>
        <NotificationsSelectTime label="Time" schedule={schedule} setSchedule={setSchedule} />
      </Box>

      <Box width={width} sx={timezoneContainerSx}>
        <NotificationsSelectTimezone
          label="Timezone"
          schedule={schedule}
          setSchedule={setScheduleFromNotification}
          MenuProps={{
            style: timezoneMenuStyle,
            anchorOrigin: { vertical: "top", horizontal: "center" },
            transformOrigin: { vertical: "bottom", horizontal: "center" },
          }}
        />
      </Box>
    </Box>
  )
}

export { BriefNotificationsTimeStep }
